import React from "react";
import Logo from "./Logo/Logo";
import Hamburger from "./Hamburger/Hamburger";
import Nav from "./Nav/Nav";

function Header({ menuClicked, updateMenuClicked }) {
  return (
    <div>
      <header>
        <Logo />
        <Hamburger
          menuClicked={menuClicked}
          updateMenuClicked={updateMenuClicked}
        />
        <Nav menuClicked={menuClicked} updateMenuClicked={updateMenuClicked} />
      </header>
    </div>
  );
}

export default Header;
