import React from "react";

export default function SkillCard({ title, icon, progress, color, aosEffect }) {
  const currentProgress = {
    width: `${progress}%`,
    background: color,
  };

  const iconColor = {
    color: color,
  };

  //custom aos to css file !!!!! ///////
  return (
    <div data-aos={aosEffect} className="skill">
      <p>{title}</p>
      <div className="progress">
        <h4 style={iconColor}>{icon}</h4>
        <div className="progress-bar">
          <div className="progress-current" style={currentProgress} />
        </div>
      </div>
    </div>
  );
}
