import React from "react";

export default function SnowFall({ menuClicked, updateMenuClicked }) {
  const span1 = {
    top: "35%",
    transform: "rotate(225deg)",
  };
  const span2 = {
    opacity: "0",
  };
  const span3 = {
    top: "-35%",
    transform: "rotate(-225deg)",
  };

  return (
    <div className="hamburger" onClick={updateMenuClicked}>
      <span style={menuClicked ? span1 : null} />
      <span style={menuClicked ? span2 : null} />
      <span style={menuClicked ? span3 : null} />
    </div>
  );
}
