import React from "react";
import pfImg from "./pfImg.png";

function Landing() {
  return (
    <div className="landing">
      <div className="img-container">
        <img src={pfImg} alt="just me" />
      </div>

      <div className="landing-title">
        <h1>Robert Szathmari</h1>
        <h3>You dream it, I make it</h3>
        <div className="landing-btn">
          <span></span>
          <span></span>
          <span></span>
          <span></span>
          <a href="#projectTitle">
            <h4>Learn More</h4>
          </a>
        </div>
      </div>
    </div>
  );
}

export default Landing;
