import React, { useEffect } from "react";
import ContactHeader from "./ContactHeader/ContactHeader";
import ContactCard from "./ContactCard/ContactCard";
import { MdPhoneAndroid, MdMail } from "react-icons/md";
import { FaGithub } from "react-icons/fa";

import AOS from "aos";
import "aos/dist/aos.css";

export default function Contact() {
  useEffect(() => {
    AOS.init();
    AOS.refresh();
  });
  return (
    <div className="contact">
      <div id="contact"></div>
      <ContactHeader />
      <div className="contactCard-container">
        <ContactCard
          link={"mailto:szathrobi98@gmail.com"}
          icon={<MdMail className="contact-icon" />}
          title="Email"
          description="szathrobi98@gmail.com"
          aosEffect="slide-right"
          target="self"
        />
        <ContactCard
          link={"!#"}
          icon={<MdPhoneAndroid className="contact-icon" />}
          title="Phone"
          description="+36305379625"
          prevDef={true}
          aosEffect="slide-up"
          target="self"
        />
        <ContactCard
          link={"https://github.com/szathrobi"}
          icon={<FaGithub className="contact-icon" />}
          title="Github"
          description="github.com/SzathRobi"
          aosEffect="slide-left"
          target="blank"
        />
      </div>
    </div>
  );
}
