import React from "react";

export default function Contact({
  link,
  icon,
  title,
  description,
  prevDef,
  target,
  aosEffect,
}) {
  const prevDefault = (event) => event.preventDefault();

  return (
    <div data-aos={aosEffect} className="contact-card">
      <a href={link} onClick={prevDef ? prevDefault : null} target={target}>
        <h1>{icon}</h1>
        <h3>{title}</h3>
        <h5>{description}</h5>
      </a>
    </div>
  );
}
