import React from "react";

export default function Nav({ menuClicked, updateMenuClicked }) {
  const slideIn = {
    animationName: menuClicked ? "slideIn" : "",
  };

  return (
    <nav>
      <a href="#app" style={slideIn} onClick={updateMenuClicked}>
        Home
      </a>

      <a href="#projects" style={slideIn} onClick={updateMenuClicked}>
        Projects
      </a>

      <a href="#about" style={slideIn} onClick={updateMenuClicked}>
        About
      </a>

      <a href="#contact" style={slideIn} onClick={updateMenuClicked}>
        Contact
      </a>
    </nav>
  );
}
