import React from "react";
import { FaEye, FaCode } from "react-icons/fa";

export default function Projects({
  img,
  title,
  desc,
  demoLink,
  codeLink,
  aosEffect,
}) {
  return (
    <div data-aos={aosEffect} className="Project">
      <img src={img} alt="something" />
      <h2>{title}</h2>
      <div className="project-footer">
        <a href={demoLink} target="blank">
          <FaEye className="project-icon" />
          <h4>Live Demo</h4>
        </a>
        <a href={codeLink} target="blank">
          <FaCode className="project-icon" />
          <h4>The Code</h4>
        </a>
      </div>
    </div>
  );
}
