import React, { useEffect } from "react";

import { FaIdCard, FaRegGrinHearts, FaLaptop } from "react-icons/fa";

export default function Mystory() {
  return (
    <section className="my-story">
      <div data-aos="flip-right" className="story">
        <FaIdCard className="story-icon" />
        <p>
          My name is Róbert Szathmári and I am a frontend developer in Budapest,
          Hungary.
        </p>
      </div>
      <div data-aos="flip-left" className="story">
        <FaRegGrinHearts className="story-icon" />
        <p>
          Programming is a passion for me and because of that, I am a highly
          motivated, curius person in this field, who always seeking for new
          knowledge and challenges.
        </p>
      </div>
      <div data-aos="flip-up" className="story">
        <FaLaptop className="story-icon" />
        <p>
          Every day, I learn something new, or deepen my knowledge in a subject
          to build more complex websites and applications with an appealing
          design.
        </p>
      </div>
    </section>
  );
}
